/* 스킵 메뉴 */
#skip {
    position: relative;
    z-index: 100000;
}

#skip a {
    background: #000;
    color: #fff;
    position: absolute;
    padding: 20px 40px;
    left: -400px;
    top: 5px;
}
#skip a:active,
#skip a:focus{
    left: 5px;
}

/* 컨테이너 */
.container {
    width: 1160px;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;
    transition: all 0.5s ease;
}

/* 섹션 공통요소 */
.section {
    padding: 120px 0;
}
.section > h2 {
    font-size: 50px;
    line-height: 1;
    text-align: center;
    margin-bottom: 20px;
}
.section > p {
    font-size: 22px;
    font-weight: 300;
    color: #666;
    text-align: center;
    margin-bottom: 70px;
}
.section > span {
    display: block;
    text-align: center;
    font-size: 16px;
    text-decoration: underline;
    text-underline-position: under;
    margin-bottom: 20px;
}
/* 폰트 */
.nexon{
    font-family: 'NexonLv1Gothic';
    font-weight: 400;
}

/* 배경 */
body{
    background-color: #f8f8f8;
}
.gray{
    background-color: #f5f5f5;
}
/* fixed */
body{
    overflow: overlay;
}
body.fixed{
    height: 100vh;
    overflow: hidden;
}

/* 간격셋팅 */
.mb0 {margin-bottom: 0 !important;}
.mb10 {margin-bottom: 10px !important;}
.mb20 {margin-bottom: 20px !important;}
.mb30 {margin-bottom: 30px !important;}
.mb40 {margin-bottom: 40px !important;}
.mb50 {margin-bottom: 50px !important;}
.mb60 {margin-bottom: 60px !important;}
.mb70 {margin-bottom: 70px !important;}

.mt0 {margin-top: 0 !important;}
.mt10 {margin-top: 10px !important;}
.mt20 {margin-top: 20px !important;}
.mt30 {margin-top: 30px !important;}
.mt40 {margin-top: 40px !important;}
.mt50 {margin-top: 50px !important;}
.mt60 {margin-top: 60px !important;}
.mt70 {margin-top: 70px !important;}


@media (max-width: 1280px) {
    .container{
        width: 96%;
    }
    .section > p {
        margin-left: 10px;
        margin-right: 10px;
    }
}
@media (max-width: 768px) {
    .container{
        width: 100%;
    }
    .section > h2 {
        font-size: 40px;
    }
    .section > p {
        font-size: 20px;
        margin-left: 17px;
        margin-right: 17px;
        word-break: keep-all;
        margin-bottom: 30px;
    }
}
@media (max-width: 480px){
    .image__inner{
        display: block;
    }
    .section > h2 {
        font-size: 30px;
        margin-left: 20px;
        margin-right: 20px;
        
    }
    .section > p {
        font-size: 16px;
    }
}