
.footer__menu {
    display: flex;
    margin-bottom: 70px;
    justify-content: space-between;
  }
  .footer__menu > div {
    width: 16%;
  }
  .footer__menu h3 {
    font-size: 18px;
    margin-bottom: 20px;
  }
  
  .footer__menu li a {
    font-size: 14px;
    color: #666;
    margin: 10px 0;
    display: block;
  }
  .footer__right {
    border-top: 1px solid #d9d9d9;
    text-align: center;
    padding-top: 40px;
    color: #666;
    line-height: 1.5;
  }
  
  @media (max-width: 960px) {
    .footer__menu{
      display: none;
    }
    .footer__right{
      padding-top: 0;
      border-top: 0;
    }
    .footer__wrap {
      padding: 40px 0;
    }
  }
  
