.header__inner {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #ccc;
    position: fixed;
    left: 0;
    top: 0;
    background: #fff;
    z-index: 1000000;
}

.header__logo {
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    width: 20%;
}

.header__logo em {
    font-size: 18px;
    font-weight: 400;
}

.header__menu {
    width: 65%;
    display: flex;
    justify-content: center;
}


.header__menu li {
    display: inline;
}

.header__menu li a {
    transition: all 0.2s ease;
}

.header__menu li a.active {
    background-color: #000;
    color: white;
    border-radius: 5px;
}

.header__menu li a {
    padding: 13px 20px;
    margin: 0 5px;
}

.header__menu li a:hover {
    background-color: #f1f1f1;
    border-radius: 5px;
    text-decoration: underline;
}

.header__menu li a.active:hover {
    background-color: #000;
    color: white;
    border-radius: 5px;
    text-decoration: underline;
}

.header__member {
    width: 15%;
    text-align: right;
}

.header__member a {
    font-size: 16px;
    border: 1px solid black;
    padding: 10px 30px;
    border-radius: 50px;
}

.header__member a:hover {
    background-color: #000;
    color: white;
}

/* 반응형일때 사이드바로 만들기 */
.header__ham {
    position: absolute;
    right: 16px;
    top: 10px;
    z-index: 1000;
    width: 50px;
    height: 50px;
    display: none;
    cursor: pointer;
}

.header__ham span {
    display: block;
    background: black;
    width: 30px;
    height: 2px;
    border-radius: 3px;
    margin-left: 17px;
    margin-top: 7px;
    transition: 0.25s margin 0.25s, 0.25s transform, 0.25s opacity 0.4s;
}

.header__ham span:nth-child(1) {
    margin-top: 13px;
}

.header__ham span:nth-child(2) {
    opacity: 1;
}

.header__ham.active span {
    transition: 0.25s margin, 0.25s transform 0.25s;
}

.header__ham.active span:nth-child(1) {
    margin-top: 25px;
    margin-bottom: -9px;
    transform: rotate(45deg);
}

.header__ham.active span:nth-child(2) {
    transform: rotate(45deg);
    opacity: 0;
}

.header__ham.active span:nth-child(3) {
    margin-top: -2px;
    transform: rotate(-45deg);

}

@media (max-width: 1300px) {
    .header__logo {
        width: 50%;
    }

    .header__member {
        width: 50%;
        margin-right: 50px;
    }

    .header__menu {
        position: fixed;
        right: -100%;
        top: 69px;
        background: #fff;
        width: 40%;
        height: 100vh;
        padding: 20px;
        text-align: right;
        transition: all 0.4s ease-in;
        justify-content: end;
    }

    .header__menu ul {
        width: 100%;
    }

    .header__menu ul li {
        display: block;
        margin: 20px;
        margin-bottom: 35px;
    }

    .header__menu ul li a {
        white-space: nowrap;
        font-size: 16px;
    }

    .header__menu.overlay {
        right: 0;
    }

    .header__ham {
        display: block;
    }
}

@media (max-width: 600px) {
    .header__logo {
        font-size: 20px;
    }
}